<template>
    <div class="income-reading">
        <nav-bar
            title="阅读收益"
            :hairline="false" />
        <div class="board">
            <div class="board-info">
                <div class="board-stats">
                    <div class="board-number time">{{ readingTime }}</div>
                    <div class="board-label">今日已阅读</div>
                </div>
                <div class="board-stats Hairline Hairline_left">
                    <div class="board-number coin">{{ readingCoin }}</div>
                    <div class="board-label">今日阅读收益</div>
                </div>
            </div>
        </div>
        <div class="content">
            <income-container title="赚钱小技巧">
                <ul class="tips">
                    <li
                        v-for="tip in tips"
                        :key="tip"
                        v-html="tip" />
                </ul>
            </income-container>
            <income-container title="今日阅读收益">
                <record-list
                    empty-text="暂无阅读收益"
                    :list="recordList"
                    :finished="true">
                    <income-list
                        slot="list"
                        :income-list="recordList"/>
                </record-list>
            </income-container>
        </div>

        <div
            class="footer-income"
            @click="goLottery">
            <img
                :src="footerBanner.imgUrl"
                alt="">
        </div>
    </div>
</template>

<script>
import NavBar from '@/components/NavBar';
import RecordList from '@/containers/RecordList';
import { getConfigByKey, getClientConfig } from '@/api/common';
import { getReadingIncome } from '@/api/income';
import { openCommonWebViewByPath } from '@/utils/jsApiV2/client';
import config from '@/config';
import IncomeContainer from './IncomeContainer';
import IncomeList from './IncomeList';
import {
    apolloIncomeTipKey,
    tips,
    apolloIncomeFooterBanner,
    defaultFooterBanner,
} from './config';

export default {
    name: 'IncomeReading',
    components: {
        NavBar,
        IncomeContainer,
        RecordList,
        IncomeList,
    },
    data() {
        return {
            tips,
            footerBanner: {
                link: '',
                title: '',
                imgUrl: '',
            },
            recordList: [],
            readingTime: 0,
            readingCoin: 0,
        };
    },
    computed: {
        version() {
            const deviceInfo = config.deviceInfo || {};

            return Number(deviceInfo.version_code);
        },
    },
    created() {
        this.$root.hidePlaceholder();
        this.getReadingTime();
        this.getTips();
        this.getIncome();
        this.getApolloIncomeFooterBanner();
    },
    methods: {
        getReadingTime() {
            const readingTime = this.$route.query.readTime;

            this.readingTime = parseInt(readingTime, 10) || 0;
        },
        getTips() {
            getConfigByKey({
                key: apolloIncomeTipKey,
            })
                .then((res) => {
                    const content = res.data.content || {};

                    if (this.version < 40740) {
                        this.tips = content.old;
                    } else {
                        this.tips = content[40740];
                    }
                });
        },
        resetReadingTime(newsCount, videoCount) {
            if (this.version && this.version <= 40703) {
                getClientConfig()
                    .then((res) => {
                        const videoCircleDuration = parseInt(res.videoCircleDuration, 10) / 60000;
                        const newsCircleDuration = parseInt(res.newsCircleDuration, 10) / 60000;
                        const readingTimeByServer = (newsCount * newsCircleDuration)
                            + (videoCount * videoCircleDuration);

                        this.readingTime = readingTimeByServer;
                    });
            }
        },
        getIncome() {
            getReadingIncome()
                .then((res) => {
                    this.readingCoin = res.browseIncome;
                    this.recordList = res.recordList;

                    // TODO: 临时代码，为了修复客户端 bug
                    const newsCount = res.newsCount;
                    const videoCount = res.videoCount;

                    this.resetReadingTime(newsCount, videoCount);
                })
                .catch(() => this.$error());
        },
        getApolloIncomeFooterBanner() {
            getConfigByKey({
                key: apolloIncomeFooterBanner,
            })
                .then((res) => {
                    const footerBannerConfig = res.data.incomeReading;
                    const randomIndex = parseInt(Math.random() * footerBannerConfig.length, 10);
                    this.footerBanner = footerBannerConfig[randomIndex];
                    this.$o2o({
                        e_c: '任务',
                        e_a: '曝光',
                        e_n: '阅读收益曝光',
                        e_x: JSON.stringify({
                            name: this.footerBanner.title,
                        }) });
                })
                .catch(() => {
                    // 当阿波罗配置报错或者超时 给定默认值
                    this.footerBanner = defaultFooterBanner;
                    this.$o2o({
                        e_c: '任务',
                        e_a: '曝光',
                        e_n: '阅读收益曝光',
                        e_x: JSON.stringify({
                            name: this.footerBanner.title,
                        }) });
                });
        },
        goLottery() {
            openCommonWebViewByPath(`${this.footerBanner.link}`);
            this.$o2o({
                e_c: '任务',
                e_a: '点击',
                e_n: '阅读收益底部按钮点击',
                e_x: JSON.stringify({
                    name: this.footerBanner.title,
                }) });
        },
    },
};
</script>

